#background {
  width: 100%;
  height: 875px;
  background-color: rgb(250, 249, 243);
  position: relative;
}


.array-container {
  position:absolute;
  bottom:0;
  width: 100%;
  text-align: center;
  }
  
.array-bar {
  width: 8px;
  display: inline-block;
  margin: 0 1px;
}

 
#toolbar-container {
  width: 100%;
  height: 100px;
  background-color: rgb(172, 141, 120);
}

#generateArray {
  font-size: 16px;
  font-family: monospace;
  display: inline-block;
  margin-left: 25px;
  margin-right: 15px;
  cursor: pointer;
}
    
#generateArray:hover {
  color: rgb(212, 212, 212) !important;
}

.separator {
  position: relative;
  width: 5px;
  height: 55px;
  background-color: rgba(124, 106, 106, 0.8);
  margin-left: 15px;
  margin-right: 15px;
  display: inline-block;
  top: 25%;
}
    
.Button {
  color: white;
  font-size: 16px;
  font-family: monospace;
  display: inline-block;
  margin-left: 15px;
  margin-right: 15px;
  cursor: pointer;
}
    
.Button:hover {
  color: rgb(212, 212, 212);
}